<template>
  <div>
    <div :id="id" :class="className" :style="{height:height,width:width}" />
  </div>
</template>

<script>
import echarts from "echarts";

export default {
  props: {
    className: {
      type: String,
      default: "chart"
    },
    id: {
      type: String,
      default: "chart"
    },
    width: {
      type: String,
      default: "200px"
    },
    height: {
      type: String,
      default: "200px"
    }
  },
  data() {
    return {
      chart: null
    };
  },
  mounted() {
    this.initChart();
  },
  beforeDestroy() {
    if (!this.chart) {
      return;
    }
    this.chart.dispose();
    this.chart = null;
  },
  methods: {
    initChart() {
      this.chart = echarts.init(document.getElementById(this.id));
      this.chart.setOption({
        title: {
          text: "六大能力得分",
          left: "left",
          padding: 20,

          textStyle: {
            height: 30,
            fontSize: "18",
            color: "#333"
          }
        },
        tooltip: {
          trigger: "axis",

          formatter: function(params) {
            //提示信息，为空时，显示成0.00
            let toolTips = "";
            for (let i = 0; i < params.length; i++) {
              if (params[i].value !== 0) {
                toolTips +=
                  params[i].marker +
                  params[i].seriesName +
                  "：" +
                  params[i].value +
                  "<br/>";
              }
            }
            return toolTips;
          }
        },
        legend: {
          itemWidth: 15,
          itemHeight: 15,
          data: ["带动能力", "不可用"]
        },
        xAxis: {
          data: [
            "带动能力",
            "盈利能力",
            "营运能力",
            "成长能力",
            "偿债能力",
            "创新能力"
          ],
          splitLine: {
            show: false
          }
        },
        yAxis: {
          splitLine: {
            show: true
          }
        },
        label: {
          show: true,
          position: "insideTop",
          formatter: function(params) {
            if (params.value > 0) {
              return `${params.seriesName} ${params.value}`;
            } else {
              return "";
            }
          }
        },
        series: [
          {
            name: "带动农户数",
            type: "bar",
            stack: "a",

            data: [5, 0, 0, 0, 0, 0]
          },
          {
            name: "每户增收",
            type: "bar",
            stack: "a",
            data: [5, 0, 0, 0, 0, 0]
          },
          {
            name: "从业人数",
            type: "bar",
            stack: "a",
            data: [5, 0, 0, 0, 0, 0]
          },
          {
            name: "利润总额",
            type: "bar",
            stack: "a",
            data: [0, 5, 0, 0, 0, 0]
          },
          {
            name: "收入利润率",
            type: "bar",
            stack: "a",
            data: [0, 5, 0, 0, 0, 0]
          },
          {
            name: "总资产报酬率",
            type: "bar",
            stack: "a",
            data: [0, 5, 0, 0, 0, 0]
          },
          {
            name: "净资产收益率",
            type: "bar",
            stack: "a",
            data: [0, 10, 0, 0, 0, 0]
          },
          {
            name: "总资产周转率",
            type: "bar",
            stack: "a",
            data: [0, 0, 8, 0, 0, 0]
          },
          {
            name: "流动资产周转率",
            type: "bar",
            stack: "a",
            data: [0, 0, 2, 0, 0, 0]
          },
          {
            name: "专门质检机构",
            type: "bar",
            stack: "a",
            data: [0, 0, 2, 0, 0, 0]
          },
          {
            name: "质量体系认证",
            type: "bar",
            stack: "a",
            data: [0, 0, 2, 0, 0, 0]
          },
          {
            name: "出口创汇占销售收入\n比重过半",
            type: "bar",
            stack: "a",
            data: [0, 0, 2, 0, 0, 0]
          },
          {
            name: "总资产",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 5, 0, 0]
          },
          {
            name: "收入",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 5, 0, 0]
          },
          {
            name: "总资产增长率",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 5, 0, 0]
          },
          {
            name: "销售收入增长率",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 5, 0, 0]
          },
          {
            name: "资产负债率",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 0, 10, 0]
          },
          {
            name: "官网",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 0, 0, 2]
          },
          {
            name: "专利",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 0, 0, 2]
          },
          {
            name: "高企",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 0, 0, 2]
          },
          {
            name: "大专及以上人员占比",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 0, 0, 2]
          },
          {
            name: "研发经费投入占\n销售收入比重",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 0, 0, 2]
          },
          {
            name: "研发经费",
            type: "bar",
            stack: "a",
            data: [0, 0, 0, 0, 0, 2]
          }
        ]
      });
    }
  }
  // watch: {
  //   options: {
  //     handler(options) {
  //       this.chart.setOption(this.options);
  //     },
  //     deep: true
  //   }
  // }
};
</script>